<template>
  <v-app>
    <template>
      <!-- 目錄 -->
      <template>
        <v-tabs
          v-model="tabs"
          centered
          icons-and-text
          dark
          background-color="grey darken-3"
        >
          <v-tabs-slider></v-tabs-slider>
          <v-tab> 成智語典<v-icon>mdi-school</v-icon> </v-tab>

          <v-tab> 歷史紀錄<v-icon>mdi-clipboard-text-clock</v-icon> </v-tab>

          <v-tab> 成就統計<v-icon>mdi-star-box-multiple</v-icon> </v-tab>
        </v-tabs>
      </template>

      <!-- 成智語典 -->
      <v-main v-if="tabs == 0">
        <!-- 顯示框 -->
        <v-row class="mx-10 pb-2">
          <v-col cols="3" class="text-center">
            <v-text-field
              label=""
              outlined
              disabled
              ref="wordTextField0"
              :value="words[0] === null ? null : displayedWords[words[0] + 0]"
              @input="updateWord(0, $event)"
              class="large-text"
            ></v-text-field>
          </v-col>
          <v-col cols="3">
            <!-- <v-text-field label="" outlined disabled ref="wordTextField2"></v-text-field> -->
            <v-text-field
              label=""
              outlined
              disabled
              ref="wordTextField1"
              :value="words[1] === null ? null : displayedWords[words[1] + 7]"
              class="large-text"
              @input="updateWord(1, $event)"
            ></v-text-field>
          </v-col>
          <v-col cols="3">
            <!-- <v-text-field label="" outlined disabled ref="wordTextField3"></v-text-field> -->
            <v-text-field
              label=""
              outlined
              disabled
              ref="wordTextField2"
              :value="words[2] === null ? null : displayedWords[words[2] + 14]"
              class="large-text"
              @input="updateWord(2, $event)"
            ></v-text-field>
          </v-col>
          <v-col cols="3">
            <!-- <v-text-field label="" outlined disabled ref="wordTextField4"></v-text-field> -->
            <v-text-field
              label=""
              outlined
              disabled
              ref="wordTextField3"
              :value="words[3] === null ? null : displayedWords[words[3] + 21]"
              class="large-text"
              @input="updateWord(3, $event)"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row justify="space-around" class="pb-4">
          <p>猜測次數 : {{ guessCount }}</p>
          <!-- <p>{{ history }}</p> -->
        </v-row>

        <!-- 鍵盤 -->
        <v-row align="center" justify="center" class="pb-6">
          <v-col
            v-for="colIndex in 4"
            :key="'col-' + colIndex"
            class="d-flex flex-column align-center py-1 col-12"
          >
            <v-btn-toggle v-model="words[colIndex - 1]">
              <v-btn
                v-for="rowIndex in 7"
                :key="'row-' + rowIndex"
                @click="btn_main_click(colIndex - 1, rowIndex - 1)"
                :color="getColor(colIndex - 1, rowIndex - 1)"
                :disabled="shouldDisable(colIndex - 1, rowIndex - 1)"
              >
                {{ displayedWords[rowIndex - 1 + (colIndex - 1) * 7] }}
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <!-- 送出&清除 -->
        <v-row justify="space-around" class="pt-1">
          <v-btn @click="btn_clear">
            <v-icon>mdi-close-thick</v-icon>清除
          </v-btn>
          <v-btn color="primary" @click="btn_check">
            <v-icon>mdi-check-bold</v-icon>送出
          </v-btn>
        </v-row>

        <!-- 錯誤提示 -->
        <v-snackbar v-model="snackbar_air">
          請確保所有選項都已選擇！
          <template v-slot:action="{ attrs }">
            <v-btn
              color="pink"
              text
              v-bind="attrs"
              @click="snackbar_air = false"
            >
              Close
            </v-btn>
          </template>
        </v-snackbar>

        <v-snackbar v-model="snackbar_err">
          答案錯誤! 再接再厲!
          <template v-slot:action="{ attrs }">
            <v-btn
              color="pink"
              text
              v-bind="attrs"
              @click="snackbar_err = false"
            >
              Close
            </v-btn>
          </template>
        </v-snackbar>

        <!-- 答對提示 -->
        <v-dialog v-model="dialog" max-width="290">
          <v-card>
            <v-card-text>
              <div class="py-3 teal--text text--lighten-2">恭喜答對</div>
              <p class="text-h5 text--primary">
                <b> {{ correctIdiom }}</b>
              </p>
              <div class="text--primary">
                {{ correctExplanation }}
              </div>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="func_reset">下一題</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- 全解重置 -->
        <v-dialog v-model="win" max-width="290">
          <v-card>
            <v-card-text>
              <div class="py-3 teal--text text--lighten-2">恭喜全解</div>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="win=!win">重置系統</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-main>

      <!-- 歷史紀錄 -->
      <v-main v-if="tabs == 1">
        <template>
          <v-card class="mx-auto" max-width="344" v-if="history.length == 0">
            <v-card-text>
              <p class="text-h5 text--primary pt-0">
                <b>尚未留下紀錄</b>
              </p>
              <div class="text--primary">快去成智語典練習吧!</div>
            </v-card-text>
          </v-card>

          <v-row class="px-5">
            <v-col cols="12" md="4" lg="3" v-for="idx in reversedHistory">
              <v-card class="mx-auto py-1" max-width="344">
                <v-card-text>
                  <div>{{ idx.time }}</div>
                  <p class="text-h5 text--primary pt-3">
                    <b>{{ idx.idiom }}</b>
                  </p>
                  <p>猜測次數 : {{ idx.guessCount }}</p>
                  <div class="text--primary">
                    {{ idx.explanation }}
                  </div>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </template>
      </v-main>

      <!-- 成就統計 -->
      <v-main v-if="tabs == 2">
        <v-card class="mx-auto" max-width="">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>統計資料</b>
            </p>
            <div class="text--primary">
              已練習成語 : {{ history.length }}<br />
              總成語數量 : {{ idioms.length }}
            </div>
          </v-card-text>
        </v-card>

        <v-card class="mx-auto" max-width="" v-if="history.length == 0">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>尚未取得成就</b>
            </p>
            <div class="text--primary">快去成智語典練習吧!</div>
          </v-card-text>
        </v-card>

        <v-card class="mx-auto" max-width="" v-if="history.length >= 100">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>百折不撓</b>
            </p>
            <div class="text--primary">成功完成100題</div>
          </v-card-text>
        </v-card>

        <v-card class="mx-auto" max-width="" v-if="history.length >= 30">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>韋編三絕</b>
            </p>
            <div class="text--primary">成功完成30題</div>
          </v-card-text>
        </v-card>

        <v-card class="mx-auto" max-width="" v-if="history.length >= 10">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>聞一知十</b>
            </p>
            <div class="text--primary">成功完成10題</div>
          </v-card-text>
        </v-card>

        <v-card class="mx-auto" max-width="" v-if="history.length >= 3">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>舉一反三</b>
            </p>
            <div class="text--primary">成功完成3題</div>
          </v-card-text>
        </v-card>

        <v-card class="mx-auto" max-width="" v-if="history.length >= 1">
          <v-card-text>
            <p class="text-h5 text--primary pt-0">
              <b>初出茅廬</b>
            </p>
            <div class="text--primary">成功完成1題</div>
          </v-card-text>
        </v-card>
      </v-main>
    </template>
  </v-app>
</template>

<style>
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+TC&display=swap");

* {
  font-family: "Noto Sans TC", sans-serif;
}

input {
  text-align: center;
}

.large-text input {
  font-size: 28.5px;
  /* 調整此值以達到所需的大小 */
}

.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
</style>

<script>
export default {
  name: "App",
  data() {
    return {
      tabs: null,

      words: [null, null, null, null],
      snackbar_air: false,
      snackbar_err: false,
      dialog: false,
      win:false,

      randomWords: [
        "心",
        "馬",
        "龍",
        "月",
        "風",
        "花",
        "海",
        "雲",
        "木",
        "火",
        "山",
        "石",
        "水",
        "虎",
        "鳥",
        "魚",
        "龜",
        "蛇",
        "蝶",
        "象",
        "狗",
        "貓",
        "猴",
        "兔",
        "雁",
        "梅",
        "竹",
        "菊",
        "蘭",
        "桃",
        "歌",
        "舞",
        "笑",
        "泣",
        "夢",
        "愛",
        "思",
        "慧",
        "勇",
        "忠",
        "誠",
        "孝",
        "仁",
        "義",
        "禮",
        "智",
        "信",
        "和",
        "平",
        "容",
      ],

      displayedWords: Array(28).fill(null),

      word_ans: "",
      expl_ans: "",
      correctIdiom: "",
      correctExplanation: "",
      guessCount: 0,

      num_ans: [],
      locked: [false, false, false, false],
      errorChoices: [],

      idioms: [],
      explanations: [],
      correctIdioms: [],
      history: [],
      once: false,
    };
  },
  methods: {
    // 按鍵盤
    btn_main_click(colIndex, rowIndex) {
      if (this.getColor(colIndex, rowIndex) == "green") {
        console.log("green");
        // this.words[colIndex] = rowIndex;
      }
    },
    // 送出答案
    btn_check() {
      const hasNullOrEmpty = this.words.some(
        (item) => item == null || item.length == 0
      );

      if (hasNullOrEmpty) {
        this.snackbar_air = true;
      } else {
        this.words.forEach((word, index) => {
          if (word == this.num_ans[index]) {
            this.$refs[`wordTextField${index}`].value = this.word_ans[index];
          } else {
            this.$refs[`wordTextField${index}`].value = "";
          }
        });

        if (this.once == false) {
          this.guessCount += 1;
        }

        // 檢查 word 陣列中的四個值是否正確
        const isCorrect = this.words.every(
          (word, index) => word === this.num_ans[index]
        );

        if (isCorrect) {
          if (this.once == false) {
            this.once = true;
          }
          console.log("所有值都正確！");
          this.correctIdiom = this.word_ans;
          this.correctExplanation = this.expl_ans;
          this.dialog = true;
        } else {
          this.$nextTick(() => {
            this.words.forEach((word, index) => {
              if (word == this.num_ans[index]) {
                this.locked[index] = true;
              } else {
                this.errorChoices.push(word + index * 7);
                this.words[index] = null;
              }
            });
          });
          this.snackbar_err = true;

          console.log("至少有一個值不正確。");
        }
      }
    },
    // 清除鍵盤值
    btn_clear() {
      this.words = [null, null, null, null];
    },
    // 重置所有值
    func_reset() {
      this.dialog = false;
      this.btn_clear();
      this.locked = [false, false, false, false];
      this.history.push({
        idiom: this.correctIdiom,
        explanation: this.correctExplanation,
        guessCount: this.guessCount,
        time: new Date().toLocaleString(),
      });
      this.guessCount = 0;
      this.once = false;
      this.errorChoices = [];

      if(this.history.length%100 == 0){
        this.win=true;
        this.correctIdioms = [];
      }
      const { idiom, explanation } = this.getRandomIdiomAndExplanation();
      this.word_ans = idiom;
      this.expl_ans = explanation;
      this.updateDisplayedWords();

    },
    // 取得顏色
    getColor(colIndex, rowIndex) {
      if (
        this.word_ans[colIndex] ==
          this.displayedWords[rowIndex + colIndex * 7] &&
        this.locked[colIndex]
      ) {
        return "green";
      }
      return "";
    },
    // 判斷按鈕是否應該被禁用
    shouldDisable(colIndex, rowIndex) {
      const errorIndex = this.errorChoices.indexOf(rowIndex + colIndex * 7);
      if (errorIndex !== -1) {
        return true;
      }
      return (
        this.locked[colIndex] &&
        this.displayedWords[rowIndex + colIndex * 7] !== this.word_ans[colIndex]
      );
    },
    // 更新顯示值
    updateWord(index, value) {
      this.$set(this.num_ans, index, value);
    },
    // 更新鍵盤文字
    updateDisplayedWords() {
      const usedIndexes = new Set();

      for (let i = 0; i < 28; i++) {
        let randomIndex;
        let isAns = false;

        do {
          randomIndex = Math.floor(Math.random() * this.randomWords.length);
          isAns = this.word_ans.includes(this.randomWords[randomIndex]);

          const isSameColumn = Array.from(usedIndexes).some(
            (usedIdx) =>
              usedIdx % 7 === randomIndex % 7 &&
              this.randomWords[usedIdx] === this.randomWords[randomIndex]
          );

          if (!isSameColumn && !isAns) {
            usedIndexes.add(randomIndex);
          }
        } while (usedIndexes.size <= i);

        this.displayedWords[i] = this.randomWords[randomIndex];
      }

      const ansIndexes = this.num_ans.map((num, idx) => num + idx * 7);
      ansIndexes.forEach((idx, i) => {
        this.displayedWords[idx] = this.word_ans[i];
      });
    },
    // 從試算表下載資料
    async fetchCSV() {
      try {
        const csvUrl =
          "https://docs.google.com/spreadsheets/d/e/2PACX-1vS1_ZcMDgBkq5ecnyYCIymKfSE2QPloXRZiHeeMauAZid0sZ64190AK0kzKys4_Sm4_llraEVMv6s8o/pub?gid=857759911&single=true&output=csv";

        const response = await fetch(csvUrl);
        const csvText = await response.text();

        const rows = csvText.split("\n").slice(1); // 移除表頭（第一行）
        const nonEmptyRows = rows.filter((row) => row !== "");

        nonEmptyRows.forEach((row) => {
          const [idiom, explanation] = row.split(",");
          this.idioms.push(idiom);
          this.explanations.push(explanation);
        });
      } catch (error) {
        console.error("Error fetching data from Google Sheets CSV:", error);
      }
    },
    // 隨機排列noise陣列
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
    },
    // 抽取成語&解釋
    getRandomIdiomAndExplanation() {
      let randomIndex;
      let randomIdiom;
      let randomExplanation;
      do {
        randomIndex = Math.floor(Math.random() * this.idioms.length);
        randomIdiom = this.idioms[randomIndex];
        randomExplanation = this.explanations[randomIndex];
      } while (this.correctIdioms.includes(randomIdiom));

      const randomNumbers = [];
      for (let i = 0; i < 4; i++) {
        const randomNumber = Math.floor(Math.random() * 7);
        randomNumbers.push(randomNumber);
      }
      this.num_ans = randomNumbers;

      this.shuffleArray(this.displayedWords);
      this.word_ans = randomIdiom;
      this.expl_ans = randomExplanation;
      this.displayedWords[this.num_ans[0]] = randomIdiom[0];
      this.displayedWords[this.num_ans[1] + 7] = randomIdiom[1];
      this.displayedWords[this.num_ans[2] + 14] = randomIdiom[2];
      this.displayedWords[this.num_ans[3] + 21] = randomIdiom[3];
      this.correctIdioms.push(randomIdiom);
      return { idiom: randomIdiom, explanation: randomExplanation };
    },
  },
  async created() {
    await this.fetchCSV();
    this.getRandomIdiomAndExplanation();
    this.updateDisplayedWords();
  },
  computed: {
    reversedHistory() {
      return this.history.slice().reverse();
    },
  },
};
</script>
